<template>
  <div>
    <v-container style="backgroung-color:#FAF8F6;" fluid>
      <v-layout row wrap>
        <v-flex xs12 hidden-sm-and-up style="margin-top:85%;">blah</v-flex>
        <v-flex xs12 class="text-xs-left" mt-0>
          <h1 style="color:#255F7C;">Contact Us</h1>
        </v-flex>
        <v-flex xs12 sm12 mt-0 mb-4 pa-0>
          <blockquote class="blockquote pa-0 text-xs-left">
            If you have encountered any problems with this website or have any
            suggestions, please fill out the form below and one of our
            representatives will contact you directly, to assist you with your
            requirements.
          </blockquote>
        </v-flex>
        <v-flex xs12 sm4 class="text-xs-left">
          <v-layout column>
            <v-flex xs12 style="border-bottom:1px solid #D0CECD">
              <h4 style="color:#2E7395;">Send Message</h4>
            </v-flex>
            <v-flex xs12>
              <form>
                <v-text-field
                  v-model="name"
                  :error-messages="nameErrors"
                  :counter="10"
                  label="Name"
                  required
                  @input="$v.name.$touch()"
                  @blur="$v.name.$touch()"
                ></v-text-field>
                <v-text-field
                  v-model="email"
                  :error-messages="emailErrors"
                  label="E-mail"
                  required
                  @input="$v.email.$touch()"
                  @blur="$v.email.$touch()"
                ></v-text-field>
                <v-text-field
                  v-model="phone"
                  :error-messages="phoneErrors"
                  label="Telephone"
                  required
                  @input="$v.phone.$touch()"
                  @blur="$v.phone.$touch()"
                ></v-text-field>
                <v-select
                  v-model="select"
                  :items="items"
                  :error-messages="selectErrors"
                  label="Category"
                  required
                  @change="$v.select.$touch()"
                  @blur="$v.select.$touch()"
                ></v-select>
                <v-textarea
                  outline
                  label="Message"
                  v-model="message"
                ></v-textarea>
                <v-checkbox
                  v-model="checkbox"
                  :error-messages="checkboxErrors"
                  label="I am not a Robot!"
                  required
                  @change="$v.checkbox.$touch()"
                  @blur="$v.checkbox.$touch()"
                ></v-checkbox>
                <v-layout row>
                  <v-flex xs3>
                    <v-btn color="#255F7C" small dark @click="clear"
                      >Reset</v-btn
                    >
                  </v-flex>
                  <v-spacer></v-spacer>
                  <v-flex xs3>
                    <v-btn color="#255F7C" small dark @click="submit"
                      >Submit</v-btn
                    >
                  </v-flex>
                </v-layout>
              </form>
            </v-flex>
          </v-layout>
        </v-flex>

        <v-flex xs12 sm4 offset-sm2 class="text-xs-left">
          <v-layout column>
            <v-flex xs12 style="border-bottom:1px solid #D0CECD">
              <h4 style="color:#2E7395;">Alternative</h4>
            </v-flex>
            <v-flex xs12>
              <blockquote class="blockquote pa-0 text-xs-left">
                In case your form could not reach us for any reason, you can
                contact us through our phone number and email stated below:
              </blockquote>
            </v-flex>
            <v-flex xs12>
              <v-btn
                style="color:#2E7395;border-bottom:1px solid lightgrey;"
                small
                flat
                href="tel:03-66778899"
                >03-66778899</v-btn
              >
            </v-flex>
            <v-flex xs12>
              <v-btn
                style="color:#2E7395;border-bottom:1px solid lightgrey;"
                small
                flat
                href="mailto:contact@folks.com?Subject=Hello%20again"
                >contact@folks.com</v-btn
              >
            </v-flex>
            <v-flex>
              <v-img contain src="/img/logos/cont/1.jpg"></v-img>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-container>
    <v-dialog v-model="emailProcessing" hide-overlay persistent width="300">
      <v-card color="primary" dark>
        <v-card-text>
          Sending Email...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog v-model="emailSent" hide-overlay persistent width="300">
      <v-card color="success" dark>
        <v-card-text>
          <v-icon>thumb_up</v-icon>Message successfully sent!
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  minLength,
  email
} from "vuelidate/lib/validators";
const regex = /(?=.*?\d{3}( |-|.)?\d{4})((?:\+?(?:1)(?:\1|\s*?))?(?:(?:\d{3}\s*?)|(?:\((?:\d{3})\)\s*?))\1?(?:\d{3})\1?(?:\d{4})(?:\s*?(?:#|(?:ext\.?))(?:\d{1,5}))?)\b/gi;
// const isPhone = value => regex.test(value); //phone valid

export default {
  mixins: [validationMixin],

  validations: {
    name: { required, maxLength: maxLength(10) },
    email: { required, email },
    phone: {
      required,
      minLength: minLength(6),
      phoneValid: value => regex.test(value)
    },
    select: { required },
    checkbox: {
      checked(val) {
        return val;
      }
    }
  },

  data: () => ({
    emailSent: false,
    emailProcessing: false,
    name: "",
    email: "",
    phone: "",
    message: "",
    select: null,
    items: [
      "Technical Assistance",
      "Suggestion",
      "General Assistance",
      "Other"
    ],
    checkbox: false
  }),
  watch: {
    emailProcessing(val) {
      if (!val) return;

      setTimeout(() => {
        this.emailProcessing = false;
        this.emailSent = true;
      }, 3000);
    },
    emailSent(val) {
      if (!val) return;

      setTimeout(() => (this.emailSent = false), 2000);
    }
  },

  computed: {
    checkboxErrors() {
      const errors = [];
      if (!this.$v.checkbox.$dirty) return errors;
      !this.$v.checkbox.checked && errors.push("You must agree to continue!");
      return errors;
    },
    selectErrors() {
      const errors = [];
      if (!this.$v.select.$dirty) return errors;
      !this.$v.select.required && errors.push("Category is required");
      return errors;
    },
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.maxLength &&
        errors.push("Name must be at most 10 characters long");
      !this.$v.name.required && errors.push("Name is required.");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email && errors.push("Must be valid e-mail");
      !this.$v.email.required && errors.push("E-mail is required");
      return errors;
    },
    phoneErrors() {
      const errors = [];
      if (!this.$v.phone.$dirty) return errors;
      !this.$v.phone.phoneValid && errors.push("Must be a valid phone number");
      !this.$v.phone.minLength &&
        errors.push("Phone number must be longer than 6 digits");
      !this.$v.phone.required && errors.push("Phone number is required");
      return errors;
    }
  },

  methods: {
    submit() {
      this.$v.$touch();
      if (!this.$v.$error) {
        this.emailProcessing = true;
        this.clear();
      }
    },
    validPhone(v) {
      return regex.test(v);
    },
    clear() {
      this.$v.$reset();
      this.name = "";
      this.email = "";
      this.phone = "";
      this.select = null;
      this.checkbox = false;
      this.message = "";
    }
  }
};
</script>
